import React, { useReducer, createContext } from "react";
import {
  Help,
  About,
  Resume,
  Contact,
  Skills,
  Default,
  NotFound,
} from "./Tarminalcontent";
import "../App.css";
import BioData from "./BioData";
import WebTerminal from "./WebTerminal";
// const CmdNotFound =
const initialState = [Default];
const reduce = (state = [], action) => {
  const HelpComponents = <Help value={action.type} />;
  switch (action.type) {
    case "inputValue":
      if (action.inputRef.current.value === "help") {
        state = [...state, HelpComponents];
      } else if (action.inputRef.current.value === "about") {
        state = [...state, About];
      } else if (action.inputRef.current.value === "resume") {
        state = [...state, Resume];
      } else if (action.inputRef.current.value === "contact") {
        state = [...state, Contact];
      } else if (action.inputRef.current.value === "skills") {
        state = [...state, Skills];
      } else if (action.inputRef.current.value === "clear") {
        state = [];
      } else {
        state = [...state, <NotFound cmd={action.inputRef.current.value} />];
      }
    default:
      return state;
  }
};

const myContext = createContext();
function Index() {
  const [data, dispatch] = useReducer(reduce, initialState);

  return (
    <myContext.Provider value={{ type: data, dispatch }}>
      <div className="App" onClick={() => dispatch({ type: "handleClick" })}>
        <div className="container">
          <div className="leftContent">
            <BioData />
          </div>
          <div className="rightContent">
            <WebTerminal />
          </div>
        </div>
      </div>
    </myContext.Provider>
  );
}

export default Index;
export { myContext };
